// src/pages/app/BillingPage.tsx
import React, { useEffect, useMemo, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  BillingOverviewResponse,
  PaymentSummary,
  getPaymentInvoiceUrl,
  fetchBillingOverview
} from "../../api/billing";

function getLocaleFromLanguage(language: string | undefined): string {
  const code = (language || "en").split("-")[0];
  switch (code) {
    case "it":
      return "it-IT";
    case "es":
      return "es-ES";
    default:
      return "en-GB";
  }
}

function formatAmount(cents: number, currency: string, locale: string): string {
  return new Intl.NumberFormat(locale, {
    style: "currency",
    currency: currency.toUpperCase(),
    minimumFractionDigits: 2
  }).format(cents / 100);
}

// UI-only token scaling: show tokens/10 to users, keep raw under the hood.
function tokensUi(rawTokens: number): number {
  return Math.round(rawTokens / 10);
}

// Map Stripe status -> badge class using your generic status-badge styles
function subscriptionStatusBadgeClass(status: string): string {
  const base = "status-badge ";
  switch (status) {
    case "ACTIVE":
    case "TRIALING":
      return base + "status-badge-ok";
    case "PAST_DUE":
    case "UNPAID":
    case "INCOMPLETE":
    case "INCOMPLETE_EXPIRED":
      return base + "status-badge-warn";
    case "CANCELED":
    default:
      return base + "status-badge-error";
  }
}

// Usage badge (per bot)  also uses status-badge styles
function usageStatusBadgeClass(usagePercent: number | null): string | null {
  if (usagePercent == null) return null;
  const base = "status-badge ";
  if (usagePercent >= 100) return base + "status-badge-error";
  if (usagePercent >= 90) return base + "status-badge-warn";
  if (usagePercent >= 75) return base + "status-badge-warn";
  return base + "status-badge-ok";
}

const BillingPage: React.FC = () => {
  const { t, i18n } = useTranslation();

  const locale = useMemo(
    () => getLocaleFromLanguage(i18n.resolvedLanguage || i18n.language),
    [i18n.language, i18n.resolvedLanguage]
  );

  const [data, setData] = useState<BillingOverviewResponse | null>(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);
  const [invoiceLoadingId, setInvoiceLoadingId] = useState<string | null>(null);

  const subscriptionStatusLabel = (status: string): string => {
    switch (status) {
      case "ACTIVE":
        return t("billing.subscriptionStatus.active");
      case "PAST_DUE":
        return t("billing.subscriptionStatus.pastDue");
      case "CANCELED":
        return t("billing.subscriptionStatus.canceled");
      case "TRIALING":
        return t("billing.subscriptionStatus.trial");
      case "UNPAID":
        return t("billing.subscriptionStatus.unpaid");
      case "INCOMPLETE":
        return t("billing.subscriptionStatus.incomplete");
      case "INCOMPLETE_EXPIRED":
        return t("billing.subscriptionStatus.incompleteExpired");
      default:
        return status;
    }
  };

  const usageStatusLabel = (usagePercent: number | null): string | null => {
    if (usagePercent == null) return null;
    if (usagePercent >= 100) return t("billing.usageStatus.overLimit");
    if (usagePercent >= 90) return t("billing.usageStatus.atLimit");
    if (usagePercent >= 75) return t("billing.usageStatus.highUsage");
    return t("billing.usageStatus.healthyUsage");
  };

  useEffect(() => {
    setLoading(true);
    setError(null);
    fetchBillingOverview()
      .then((res) => setData(res))
      .catch((err: any) => setError(err?.message || t("billing.errors.loadFailed")))
      .finally(() => setLoading(false));
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const handleDownloadInvoice = async (payment: PaymentSummary) => {
    if (!payment.hasInvoice) return;
    setInvoiceLoadingId(payment.id);
    setError(null);

    try {
      const { url } = await getPaymentInvoiceUrl(payment.id);
      if (url) {
        window.open(url, "_blank", "noopener,noreferrer");
      } else {
        setError(t("billing.errors.invoiceUnavailable"));
      }
    } catch (err: any) {
      console.error(err);
      setError(err?.message || t("billing.errors.invoiceDownloadFailed"));
    } finally {
      setInvoiceLoadingId(null);
    }
  };

  // -------------------------
  // Global usage aggregation
  // -------------------------

  // TOKENS (raw totals; UI displays /10)
  const totalUsedTokensRaw =
    data?.subscriptions.reduce((sum, s) => sum + s.usedTokensThisPeriod, 0) ?? 0;

  const totalLimitTokensRaw =
    data?.subscriptions.reduce((sum, s) => sum + (s.monthlyTokens || 0), 0) ?? 0;

  const hasAnyTokenLimit = totalLimitTokensRaw > 0;

  const globalTokenUsagePercent = hasAnyTokenLimit
    ? Math.min(100, Math.round((totalUsedTokensRaw / totalLimitTokensRaw) * 100))
    : null;

  const globalTokenUsageBadgeClass = usageStatusBadgeClass(globalTokenUsagePercent);
  const globalTokenUsageBadgeLabel = usageStatusLabel(globalTokenUsagePercent);

  return (
    <div>
      <div className="page-header">
        <div>
          <h1>{t("billing.title")}</h1>
          <p className="muted">{t("billing.subtitle")}</p>
        </div>
      </div>

      {error && <div className="form-error">{error}</div>}

      {loading && (
        <div className="detail-main" style={{ marginTop: "1rem" }}>
          <p>{t("billing.loading")}</p>
        </div>
      )}

      {!loading && data && (
        <>
          {/* Subscriptions + global usage summary */}
          <section className="detail-main billing-main-card">
            <div className="billing-summary-header">
              <div>
                <h2>{t("billing.activeSubscriptions")}</h2>
                <p className="muted">
                  {t("billing.totalMonthlyAcrossAllBots")}{" "}
                  <strong>{data.totalMonthlyAmountFormatted}</strong>
                </p>
              </div>

              {/* Global usage pills (Tokens + Emails) */}
              <div className="billing-summary-usage">
                {/* TOKENS */}
                <div className="billing-summary-usage-top">
                  {globalTokenUsageBadgeClass && globalTokenUsageBadgeLabel && (
                    <span className={globalTokenUsageBadgeClass}>
                      {globalTokenUsageBadgeLabel}
                    </span>
                  )}
                </div>

                <div className="billing-summary-usage-label">
                  {t("billing.thisMonth")}{" "}
                  <strong>
                    {tokensUi(totalUsedTokensRaw).toLocaleString(locale)}{" "}
                    {t("billing.tokens")}
                  </strong>
                  {hasAnyTokenLimit && (
                    <>
                      {" "}
                      <span className="muted">{t("billing.of")}</span>{" "}
                      <strong>
                        {tokensUi(totalLimitTokensRaw).toLocaleString(locale)}
                      </strong>
                    </>
                  )}
                </div>

                {hasAnyTokenLimit && globalTokenUsagePercent != null && (
                  <div className="billing-summary-usage-bar">
                    <div className="usage-bar">
                      <div
                        className="usage-bar-fill"
                        style={{ width: `${globalTokenUsagePercent}%` }}
                      />
                    </div>
                  </div>
                )}

                <p
                  className="muted"
                  style={{ marginTop: "0.4rem", fontSize: "0.82rem" }}
                >
                  {t("billing.tokensHint", {
                    defaultValue:
                      "Tokens include OpenAI, knowledge, emails and WhatsApp templates."
                  })}
                </p>
              </div>
            </div>

            {data.subscriptions.length === 0 && (
              <p className="muted" style={{ marginTop: "0.5rem" }}>
                {t("billing.noActiveSubscriptions")}
              </p>
            )}

            {data.subscriptions.length > 0 && (
              <div className="billing-subscriptions">
                {data.subscriptions.map((sub) => {
                  const currency = sub.currency || "eur";

                  // Tokens
                  const tokenUsagePercent = sub.usagePercent != null ? sub.usagePercent : null;
                  const hasTokenLimit = sub.monthlyTokens != null && sub.monthlyTokens > 0;
                  const tokenBadgeClass = usageStatusBadgeClass(tokenUsagePercent);
                  const tokenBadgeLabel = usageStatusLabel(tokenUsagePercent);


                  // Badge texts (so two badges are not ambiguous)
                  const tokenBadgeText =
                    tokenBadgeLabel != null ? `${t("billing.tokens")}: ${tokenBadgeLabel}` : null;

                  return (
<article key={sub.botId} className="billing-subscription-card billing-subscription-card--compact">
  <div className="billing-subscription-header">
    <div className="billing-subscription-title-block">
      <h3 className="billing-subscription-title">{sub.botName}</h3>
      <p className="billing-subscription-subtitle">
        {t("billing.slugLabel")}: <code>{sub.botSlug}</code>
      </p>
    </div>

    <div className="billing-subscription-right">
      <div className={subscriptionStatusBadgeClass(sub.subscriptionStatus)}>
        {subscriptionStatusLabel(sub.subscriptionStatus)}
      </div>

      <div className="billing-subscription-price">
        {sub.totalMonthlyAmountFormatted}
        {sub.usagePlanName && (
          <span className="billing-subscription-plan-label">
            /{t("billing.perMonth")}  {sub.usagePlanName}
          </span>
        )}
      </div>
    </div>
  </div>

  {/* Compact meters (2-up on desktop, stack on mobile) */}
  <div className="billing-subscription-meters">
    {/* TOKENS */}
    <div className="billing-meter">
      <div className="billing-meter-top">
        <span className="billing-meter-title">{t("billing.tokens")}</span>

        {hasTokenLimit && tokenUsagePercent != null ? (
          <span className="billing-meter-meta">
            <span className={usageStatusBadgeClass(tokenUsagePercent) ?? "status-badge"}>
              {tokenUsagePercent}%
            </span>
          </span>
        ) : (
          <span className="billing-meter-meta muted">{t("billing.noConfiguredLimit")}</span>
        )}
      </div>

      <div className="billing-meter-value">
        <strong>{tokensUi(sub.usedTokensThisPeriod).toLocaleString(locale)}</strong>
        {hasTokenLimit ? (
          <span className="muted">
            {" "}
            / {tokensUi(sub.monthlyTokens ?? 0).toLocaleString(locale)}
          </span>
        ) : null}
      </div>

      {hasTokenLimit && tokenUsagePercent != null && (
        <div className="usage-bar usage-bar--sm">
          <div className="usage-bar-fill" style={{ width: `${tokenUsagePercent}%` }} />
        </div>
      )}
    </div>
  </div>

  {/* Details (keeps cards short by default) */}
  <details className="billing-subscription-details">
    <summary className="billing-subscription-details-summary">
      {t("billing.details", { defaultValue: "Details" })}
    </summary>

    <div className="billing-subscription-details-body">
      <div className="billing-subscription-breakdown">
        <span>
          {t("billing.breakdown.features")}:{" "}
          <strong>{formatAmount(sub.featuresAmountCents, currency, locale)}</strong>
        </span>
        {sub.planAmountCents > 0 && (
          <span>
            {t("billing.breakdown.plan")}:{" "}
            <strong>{formatAmount(sub.planAmountCents, currency, locale)}</strong>
          </span>
        )}
      </div>

      {/* If you still want the verbose usage this month sentences, keep them here only */}
      {/* ...you can reuse your existing billing-usage-text blocks inside this details body... */}
    </div>
  </details>
</article>
                  );
                })}
              </div>
            )}
          </section>

          {/* Payment history */}
          <section className="detail-main" style={{ marginTop: "1.5rem" }}>
            <h2>{t("billing.paymentHistory.title")}</h2>

            {data.payments.length === 0 && (
              <p className="muted" style={{ marginTop: "0.5rem" }}>
                {t("billing.paymentHistory.empty")}
              </p>
            )}

            {data.payments.length > 0 && (
              <div className="table-responsive payments-table-wrapper">
                <table className="table" style={{ marginTop: "0.75rem" }}>
                  <thead>
                    <tr>
                      <th>{t("billing.paymentHistory.table.date")}</th>
                      <th>{t("billing.paymentHistory.table.bot")}</th>
                      <th>{t("billing.paymentHistory.table.amount")}</th>
                      <th>{t("billing.paymentHistory.table.status")}</th>
                      <th>{t("billing.paymentHistory.table.period")}</th>
                      <th />
                    </tr>
                  </thead>

                  <tbody>
                    {data.payments.map((p) => (
                      <tr key={p.id}>
                        <td>
                          {new Date(p.createdAt).toLocaleDateString(locale, {
                            year: "numeric",
                            month: "short",
                            day: "numeric"
                          })}
                        </td>
                        <td>{p.botName}</td>
                        <td>{formatAmount(p.amountCents, p.currency, locale)}</td>
                        <td>{p.status}</td>
                        <td>
                          {p.periodStart && p.periodEnd ? (
                            <>
                              {new Date(p.periodStart).toLocaleDateString(locale)} {" "}
                              {new Date(p.periodEnd).toLocaleDateString(locale)}
                            </>
                          ) : (
                            <span className="muted"></span>
                          )}
                        </td>

                        <td className="payments-actions-cell">
                          {p.hasInvoice ? (
                            <button
                              type="button"
                              className="icon-button"
                              onClick={() => handleDownloadInvoice(p)}
                              disabled={invoiceLoadingId === p.id}
                              aria-label={
                                invoiceLoadingId === p.id
                                  ? t("billing.invoice.ariaOpening")
                                  : t("billing.invoice.ariaDownload")
                              }
                              title={
                                invoiceLoadingId === p.id
                                  ? t("billing.invoice.titleOpening")
                                  : t("billing.invoice.titleDownload")
                              }
                            >
                              {invoiceLoadingId === p.id ? "" : ""}
                              <span className="sr-only">
                                {invoiceLoadingId === p.id
                                  ? t("billing.invoice.srOpening")
                                  : t("billing.invoice.srDownload")}
                              </span>
                            </button>
                          ) : (
                            <span className="muted"></span>
                          )}
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            )}
          </section>
        </>
      )}
    </div>
  );
};

export default BillingPage;
