// src/pages/app/ConversationDetailsPage.tsx
import React, { useEffect, useState } from "react";
import { Link, useParams } from "react-router-dom";
import { ConversationMessage,
  fetchConversationMessages,
  ConversationDetails,
  fetchConversationDetails,
  sendTestConversationMessage } from "../../api/bots";
import { useTranslation } from "react-i18next";

const ConversationDetailPage: React.FC = () => {
  const { t } = useTranslation();
  const { id } = useParams<{ id: string }>();

  const [messages, setMessages] = useState<ConversationMessage[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const [details, setDetails] = useState<ConversationDetails | null>(null);

  const [testText, setTestText] = useState("");
  const [sendingTest, setSendingTest] = useState(false);
  const [testError, setTestError] = useState<string | null>(null);
  const [testSuccess, setTestSuccess] = useState<string | null>(null);

 useEffect(() => {
  if (!id) return;
  setLoading(true);
  setError(null);

  Promise.all([
    fetchConversationMessages(id),
    fetchConversationDetails(id)
  ])
    .then(([messagesData, detailsData]) => {
      setMessages(messagesData);
      setDetails(detailsData);
    })
    .catch((err: any) => {
      console.error(err);
      setError(err?.message || t("conversationDetail.errors.loadFailed"));
    })
    .finally(() => setLoading(false));
}, [id, t]);

  if (!id) {
    return (
      <div className="page-container">
        <p>{t("conversationDetail.missingId")}</p>
      </div>
    );
  }

  const handleSendTest = async () => {
  if (!id) return;
  const trimmed = testText.trim();
  if (!trimmed) {
    setTestError(t("conversationDetail.testSend.empty"));
    setTestSuccess(null);
    return;
  }

  try {
    setSendingTest(true);
    setTestError(null);
    setTestSuccess(null);

    await sendTestConversationMessage(id, trimmed);
    setTestSuccess(t("conversationDetail.testSend.success"));
    setTestText("");

    // Refresh messages so the new assistant message appears
    fetchConversationMessages(id)
      .then((data) => setMessages(data))
      .catch((err) => {
        console.error("Failed to refresh messages after test send", err);
      });
  } catch (err: any) {
    console.error(err);
    setTestError(
      err?.message || t("conversationDetail.testSend.error")
    );
  } finally {
    setSendingTest(false);
  }
};

  const roleLabel = (role: string) => {
    if (role === "USER") return t("conversationDetail.roles.user");
    if (role === "ASSISTANT") return t("conversationDetail.roles.assistant");
    return t("conversationDetail.roles.system");
  };

  return (
    <div>
      <div className="page-header">
        <h1>{t("conversationDetail.title")}</h1>
        <Link to="/app/bots" className="btn-secondary">
          {t("conversationDetail.backToBots")}
        </Link>
      </div>

      {details && details.channel !== "WEB" && (
  <div className="card" style={{ marginBottom: "1rem" }}>
    <h2 style={{ marginTop: 0 }}>
      {t("conversationDetail.testSend.title")}
    </h2>
    <p className="muted" style={{ marginBottom: "0.5rem" }}>
      {t("conversationDetail.testSend.description")}
    </p>

    <div style={{ display: "flex", gap: "0.5rem" }}>
      <input
        type="text"
        value={testText}
        onChange={(e) => setTestText(e.target.value)}
        placeholder={t("conversationDetail.testSend.placeholder")}
        style={{ flex: 1 }}
      />
      <button
        type="button"
        className="btn-primary"
        onClick={handleSendTest}
        disabled={sendingTest}
      >
        {sendingTest
          ? t("conversationDetail.testSend.sending")
          : t("conversationDetail.testSend.button")}
      </button>
    </div>

    {testError && (
      <div className="form-error" style={{ marginTop: "0.5rem" }}>
        {testError}
      </div>
    )}

    {testSuccess && (
      <div className="form-success" style={{ marginTop: "0.5rem" }}>
        {testSuccess}
      </div>
    )}
  </div>
)}

      {loading && <p>{t("conversationDetail.loading")}</p>}
      {error && <div className="form-error">{error}</div>}

      {details && !loading && !error && (
    <div className="card" style={{ marginBottom: "1rem" }}>
    <h2 style={{ marginTop: 0 }}>{t("conversationDetail.parties.title")}</h2>
    <div
      className="conversation-parties"
      style={{
        display: "flex",
        gap: "1.5rem",
        flexWrap: "wrap",
        marginTop: "0.5rem"
      }}
    >
      <div style={{ flex: 1, minWidth: "200px" }}>
        <h3 style={{ marginBottom: "0.25rem" }}>
          {t("conversationDetail.parties.business")}
        </h3>
        <div className="muted">
          <div>{details.business.title}</div>
          {details.business.subtitle && (
            <div style={{ fontSize: "0.9em" }}>{details.business.subtitle}</div>
          )}
        </div>
      </div>

      <div style={{ flex: 1, minWidth: "200px" }}>
        <h3 style={{ marginBottom: "0.25rem" }}>
          {t("conversationDetail.parties.user")}
        </h3>
        <div className="muted">
          <div>
            {details.user.displayName || details.user.identifier}
          </div>
          {details.user.displayName && (
            <div style={{ fontSize: "0.85em", opacity: 0.8 }}>
            {details.user.identifier}
            </div>
          )}
        </div>
      </div>
    </div>
  </div>
)}

      {!loading && !error && messages.length === 0 && (
        <p>{t("conversationDetail.empty")}</p>
      )}

      {!loading && !error && messages.length > 0 && (
        <div className="conversation-view">
          {messages.map((m) => (
            <div
              key={m.id}
              className={`conversation-message conversation-message-${m.role.toLowerCase()}`}
            >
              <div className="conversation-meta">
                <span className="conversation-role">{roleLabel(m.role)}</span>
                <span className="conversation-time">
                  {new Date(m.createdAt).toLocaleString()}
                </span>
              </div>
              <div className="conversation-content">{m.content}</div>
            </div>
          ))}
        </div>
      )}
    </div>
  );
};

export default ConversationDetailPage;
