import { authFetchJson } from "./authorizedClient";

export type BotStatus =
  | "DRAFT"
  | "PENDING_PAYMENT"
  | "ACTIVE"
  | "SUSPENDED"
  | "CANCELED";

export type Weekday =
  | "monday"
  | "tuesday"
  | "wednesday"
  | "thursday"
  | "friday"
  | "saturday"
  | "sunday";

export interface BookingTimeWindow {
  start: string; // "HH:MM" 24h
  end: string; // "HH:MM" 24h
}

export type BookingWeeklySchedule = Partial<
  Record<Weekday, BookingTimeWindow[]>
>;

