// ---- WhatsApp templates ----

export type WhatsappTemplateStatus =
  | "APPROVED"
  | "REJECTED"
  | "PENDING"
  | "INACTIVE"
  | "PAUSED";

export interface WhatsappTemplateComponentButton {
  type: "QUICK_REPLY" | "URL" | "PHONE_NUMBER";
  text: string;
  url?: string;
  phone_number?: string;
}

export interface WhatsappTemplateComponent {
  type: "HEADER" | "BODY" | "FOOTER" | "BUTTONS";
  format?: "TEXT";
  text?: string;
  example?: {
    body_text?: string[];
    header_text?: string[];
  };
  buttons?: WhatsappTemplateComponentButton[];
}

export interface WhatsappTemplate {
  id: string;
  name: string;
  category: string;
  language: string;
  status: WhatsappTemplateStatus;
  components: WhatsappTemplateComponent[];
  rejectionReason?: string | null;
  qualityScore?: string | null;
  lastUpdatedAt?: string | null;
}

export interface WhatsappTemplateListResponse {
  items: WhatsappTemplate[];
  paging?: {
    nextCursor?: string | null;
    previousCursor?: string | null;
  };
}

export interface WhatsappTemplateFilter {
  search?: string;
  status?: WhatsappTemplateStatus | "ALL";
  category?: string | "ALL";
  language?: string;
  cursor?: string;
  limit?: number;
}

export interface WhatsappTemplateUpsertPayload {
  name: string;
  category: string;
  language: string;
  components: WhatsappTemplateComponent[];
}

export async function fetchWhatsappTemplates(
  botId: string,
  filter?: WhatsappTemplateFilter
): Promise<WhatsappTemplateListResponse> {
  const params = new URLSearchParams();

  if (filter?.search) params.set("search", filter.search);
  if (filter?.status && filter.status !== "ALL") {
    params.set("status", filter.status);
  }
  if (filter?.category && filter.category !== "ALL") {
    params.set("category", filter.category);
  }
  if (filter?.language) params.set("language", filter.language);
  if (filter?.cursor) params.set("cursor", filter.cursor);
  if (filter?.limit) params.set("limit", String(filter.limit));

  const qs = params.toString();
  const url =
    `/bots/${encodeURIComponent(botId)}/whatsapp/templates` +
    (qs ? `?${qs}` : "");

  return authFetchJson<WhatsappTemplateListResponse>(url);
}

