// src/pages/onboarding/OnboardingPlanPage.tsx
import React, { useEffect, useState } from "react";
import { useParams, useNavigate, Link } from "react-router-dom";
import { useTranslation } from "react-i18next";
import OnboardingLayout from "./OnboardingLayout";
import {
  Bot,
  getBotById,
  fetchChannels,
  BotChannel,
  ChannelType,
  getBotPricingPreview,
  BotPricingPreview,
  fetchUsagePlans,
  UsagePlan,
  startBotCheckout,
  changeBotUsagePlan
} from "../../api/bots";
import { getStoredReferralCode } from "../../utils/referral";

import starterImg from "../../assets/coslo-assist-247.png";
import growthImg from "../../assets/coslo-assist-247.png";
import scaleImg from "../../assets/coslo-assist-247.png";
import defaultPlanImg from "../../assets/coslo-assist-247.png";

const formatCurrency = (amountCents: number, currency: string) =>
  new Intl.NumberFormat("en-GB", {
    style: "currency",
    currency: currency.toUpperCase(),
    minimumFractionDigits: 2
  }).format(amountCents / 100);

const planImages: Record<string, string> = {
  starter: starterImg,
  basic: starterImg,
  growth: growthImg,
  pro: growthImg,
  scale: scaleImg,
  enterprise: scaleImg
};

const getPlanImage = (plan: UsagePlan): string => {
  const key = plan.name.toLowerCase();
  return planImages[key] ?? defaultPlanImg;
};

const OnboardingPlanPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const { t } = useTranslation();
  const navigate = useNavigate();

  const [bot, setBot] = useState<Bot | null>(null);
  const [channels, setChannels] = useState<BotChannel[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  const [pricing, setPricing] = useState<BotPricingPreview | null>(null);
  const [pricingLoading, setPricingLoading] = useState(false);
  const [pricingError, setPricingError] = useState<string | null>(null);

  const [plans, setPlans] = useState<UsagePlan[]>([]);
  const [plansLoading, setPlansLoading] = useState(false);
  const [plansError, setPlansError] = useState<string | null>(null);

  const [selectedPlanId, setSelectedPlanId] = useState<string | null>(null);
  const [checkoutLoading, setCheckoutLoading] = useState(false);
  const [success, setSuccess] = useState<string | null>(null);

  useEffect(() => {
    if (!id) return;

    const safeId = id;
    setLoading(true);
    setError(null);
    setPricingLoading(true);
    setPlansLoading(true);

    Promise.all([
      getBotById(safeId),
      fetchChannels(safeId),
      getBotPricingPreview(safeId, {}),
      fetchUsagePlans()
    ])
      .then(([botRes, channelsRes, pricingRes, plansRes]) => {
        setBot(botRes);
        setChannels(channelsRes);
        setPricing(pricingRes);
        setPlans(plansRes);
        if (plansRes.length > 0) {
          setSelectedPlanId(plansRes[0].id);
        }
      })
      .catch((err: any) => {
        console.error("Failed to load onboarding plan data", err);
        setError(err?.message || t("botPlan.errors.loadFailed"));
      })
      .finally(() => {
        setLoading(false);
        setPricingLoading(false);
        setPlansLoading(false);
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [id]);

  const channelEnabled = (type: ChannelType) =>
    channels.some((c) => c.type === type);

  const bookingEnabled = bot?.useCalendar;
  const leadAdsEnabled =
    bot &&
    (bot.leadWhatsappMessages200 ||
      bot.leadWhatsappMessages500 ||
      bot.leadWhatsappMessages1000);

  const handleBack = () => {
    if (!id) return;
    navigate(`/onboarding/bots/${encodeURIComponent(id)}/lead-ads`);
  };

  const handleSelectPlan = (planId: string) => {
    setSelectedPlanId(planId);
    setSuccess(null);
  };

  const handleActivateOrChangePlan = async () => {
    if (!id || !bot || !selectedPlanId) return;

    const isActive = bot.status === "ACTIVE";

    setCheckoutLoading(true);
    setError(null);
    setSuccess(null);

    try {
      if (!isActive) {
        const referralCode = getStoredReferralCode();
        const payload: Record<string, unknown> = { usagePlanId: selectedPlanId };
        if (referralCode) payload.referralCode = referralCode;

        const { checkoutUrl } = await startBotCheckout(id, payload as any);
        window.location.href = checkoutUrl;
      } else {
        await changeBotUsagePlan(id, { usagePlanId: selectedPlanId });
        setSuccess(t("botPlan.success.planUpdatedProration"));

        const updatedBot = await getBotById(id);
        setBot(updatedBot);
      }
    } catch (err: any) {
      console.error(err);
      setError(err?.message || t("botPlan.errors.updateFailed"));
    } finally {
      setCheckoutLoading(false);
    }
  };

  if (!id) {
    return null;
  }

  const title = t(
    "onboarding.plan.title",
    "Choose your plan and start using Coslo"
  );
  const subtitle = t(
    "onboarding.plan.subtitle",
    "Weve preconfigured your channels, bookings and Lead Ads. Now pick the plan that matches your volume."
  );

  const selectedPlan = plans.find((p) => p.id === selectedPlanId) || null;
  const combinedTotalFormatted =
    pricing && selectedPlan
      ? formatCurrency(
          pricing.totalAmountCents + selectedPlan.monthlyAmountCents,
          pricing.currency
        )
      : null;

  const isActive = bot?.status === "ACTIVE";

  return (
    <OnboardingLayout
      currentStep="plan"
      botId={id}
      title={title}
      subtitle={subtitle}
      layout="full"
    >
      <div className="onb-plan-layout">
        {error && <div className="form-error">{error}</div>}
        {success && (
          <div className="alert alert-success">
            <div className="alert-title">
              {t("botPlan.success.title", "Plan updated")}
            </div>
            <div className="alert-body">{success}</div>
          </div>
        )}

        {/* CONFIGURATION SUMMARY */}
        <section className="onb-plan-summary-card">
          <header className="onb-plan-summary-header">
            <div>
              <h2>
                {t(
                  "onboarding.plan.summaryTitle",
                  "Configuration summary"
                )}
              </h2>
              <p className="onb-plan-summary-subtitle">
                {t(
                  "onboarding.plan.summarySubtitle",
                  "Heres what youve configured for this bot so far."
                )}
              </p>
            </div>
            {bot && (
              <span className="onb-plan-status-pill">
                {t("botPlan.summary.statusLabel", "Status:")}{" "}
                <strong>{bot.status}</strong>
              </span>
            )}
          </header>

          {loading || !bot ? (
            <p className="muted">{t("common.loading", "Loading")}</p>
          ) : (
            <ul className="onb-plan-summary-list">
              <li>
                <span className="onb-plan-summary-label">
                  {t("onboarding.plan.summary.channels", "Channels")}
                </span>
                <span className="onb-plan-summary-value">
                  {["WEB", "WHATSAPP", "FACEBOOK", "INSTAGRAM"]
                    .filter((type) => channelEnabled(type as ChannelType))
                    .join(", ") ||
                    t("onboarding.plan.summary.none", "None configured")}
                  {"  "}
                  <Link
                    to={`/onboarding/bots/${encodeURIComponent(
                      bot.id
                    )}/channels`}
                  >
                    {t("onboarding.plan.edit", "Edit")}
                  </Link>
                </span>
              </li>
              <li>
                <span className="onb-plan-summary-label">
                  {t("onboarding.plan.summary.booking", "Bookings")}
                </span>
                <span className="onb-plan-summary-value">
                  {bookingEnabled
                    ? t(
                        "onboarding.plan.summary.enabled",
                        "Enabled"
                      )
                    : t(
                        "onboarding.plan.summary.disabled",
                        "Disabled"
                      )}
                  {"  "}
                  <Link
                    to={`/onboarding/bots/${encodeURIComponent(
                      bot.id
                    )}/booking`}
                  >
                    {t("onboarding.plan.edit", "Edit")}
                  </Link>
                </span>
              </li>
              <li>
                <span className="onb-plan-summary-label">
                  {t(
                    "onboarding.plan.summary.leadAds",
                    "Lead Ads  WhatsApp"
                  )}
                </span>
                <span className="onb-plan-summary-value">
                  {leadAdsEnabled
                    ? t(
                        "onboarding.plan.summary.enabled",
                        "Enabled"
                      )
                    : t(
                        "onboarding.plan.summary.disabled",
                        "Disabled"
                      )}
                  {"  "}
                  <Link
                    to={`/onboarding/bots/${encodeURIComponent(
                      bot.id
                    )}/lead-ads`}
                  >
                    {t("onboarding.plan.edit", "Edit")}
                  </Link>
                </span>
              </li>
            </ul>
          )}
        </section>

        {/* PLANS + PRICING */}
        <div className="onb-plan-grid">
          {/* LEFT: plans */}
          <section className="onb-plan-card">
            <header className="onb-plan-card-header">
              <h2>{t("botPlan.plans.title", "Plans")}</h2>
              <p className="onb-plan-card-subtitle">
                {t(
                  "botPlan.plans.subtitle",
                  "Pick the plan that matches your expected usage. You can change it later."
                )}
              </p>
            </header>

            {plansLoading && (
              <p className="muted">
                {t("botPlan.plans.loading", "Loading plans")}
              </p>
            )}
            {plansError && (
              <p className="form-error">
                {plansError}
              </p>
            )}

            {!plansLoading && plans.length === 0 && !plansError && (
              <p className="muted">
                {t("botPlan.plans.empty", "No plans available.")}
              </p>
            )}

            {plans.length > 0 && (
              <div className="onb-plan-cards">
                {plans.map((plan) => {
                  const isSelected = plan.id === selectedPlanId;
                  return (
                    <button
          key={plan.id}
          type="button"
          className={
            "onb-plan-card-item" +
            (isSelected ? " onb-plan-card-item--selected" : "")
          }
          onClick={() => handleSelectPlan(plan.id)}
        >
          <div className="onb-plan-card-inner">
            {/* LEFT: copy */}
            <div className="onb-plan-card-copy">
              <div className="onb-plan-card-name">{plan.name}</div>
              <div className="onb-plan-card-price">
                {formatCurrency(plan.monthlyAmountCents, plan.currency)}
                <span className="onb-plan-card-price-period">
                  /{t("botPlan.perMonth", "month")}
                </span>
              </div>

              {plan.description && (
                <p className="onb-plan-card-description">
                  {plan.description}
                </p>
              )}

              <ul className="onb-plan-card-meta">
                <li>
                  {t(
                    "botPlan.plans.tokensPerMonthLabel",
                    "Tokens per month"
                  )}
                  :{" "}
                  <strong>
                    {plan.monthlyTokens ??
                      t("botPlan.plans.unlimited", "Unlimited")}
                  </strong>
                </li>
              </ul>
            </div>

            {/* RIGHT: visual */}
            <div className="onb-plan-card-visual">
              <img
                src={getPlanImage(plan)}
                alt={t("botPlan.plans.planImageAlt", {
                  planName: plan.name
                })}
              />
            </div>
          </div>
        </button>
                  );
                })}
              </div>
            )}
          </section>

          {/* RIGHT: price breakdown */}
          <section className="onb-plan-card onb-plan-card--side">
            <header className="onb-plan-card-header">
              <h2>
                {t(
                  "botPlan.summary.title",
                  "Monthly cost summary"
                )}
              </h2>
              <p className="onb-plan-card-subtitle">
                {t(
                  "botPlan.summary.subtitle",
                  "Features, selected plan and combined total."
                )}
              </p>
            </header>

            <h3 className="onb-plan-side-heading">
              {t(
                "botPlan.summary.featuresSubtotal",
                "Configured features"
              )}
            </h3>

            {pricingLoading && (
              <p className="muted">
                {t(
                  "botPlan.summary.loadingPricing",
                  "Loading pricing"
                )}
              </p>
            )}
            {pricingError && (
              <p className="form-error">
                {pricingError}
              </p>
            )}

            {pricing && (
              <>
                <div className="onb-plan-summary-lines">
                  {pricing.lineItems.map((li) => (
                    <div
                      className="onb-plan-summary-line"
                      key={li.code}
                    >
                      <span className="onb-plan-summary-line-label">
                        {li.label}
                      </span>
                      <span className="onb-plan-summary-line-amount">
                        {li.monthlyAmountFormatted}/
                        {t("botPlan.perMonth", "month")}
                      </span>
                    </div>
                  ))}
                </div>

                <div className="onb-plan-summary-total">
                  <span>
                    {t(
                      "botPlan.summary.featuresTotalPerMonth",
                      "Features subtotal"
                    )}
                  </span>
                  <strong>
                    {pricing.totalAmountFormatted}
                  </strong>
                </div>
              </>
            )}

            <h3 className="onb-plan-side-heading">
              {t("botPlan.summary.selectedPlan", "Selected plan")}
            </h3>

            {selectedPlan ? (
              <div className="onb-plan-summary-lines">
                <div className="onb-plan-summary-line">
                  <span className="onb-plan-summary-line-label">
                    {selectedPlan.name}
                  </span>
                  <span className="onb-plan-summary-line-amount">
                    {formatCurrency(
                      selectedPlan.monthlyAmountCents,
                      selectedPlan.currency
                    )}
                    /{t("botPlan.perMonth", "month")}
                  </span>
                </div>
                <div className="onb-plan-summary-line">
                  <span className="onb-plan-summary-line-label">
                    {t(
                      "botPlan.plans.tokensPerMonthLabel",
                      "Tokens per month"
                    )}
                  </span>
                  <span className="onb-plan-summary-line-amount">
                    {selectedPlan.monthlyTokens ??
                      t("botPlan.plans.unlimited", "Unlimited")}
                  </span>
                </div>
              </div>
            ) : (
              <p className="muted">
                {t(
                  "botPlan.summary.noPlanSelected",
                  "Select a plan on the left to see details."
                )}
              </p>
            )}

            <div className="onb-plan-summary-total onb-plan-summary-total--strong">
              <span>
                {t(
                  "botPlan.summary.totalPerMonth",
                  "Estimated total per month"
                )}
              </span>
              <strong>
                {combinedTotalFormatted ??
                  t(
                    "botPlan.summary.selectPlanToSeeTotal",
                    "Select a plan to see the total"
                  )}
              </strong>
            </div>
          </section>
        </div>

        {/* BOTTOM ACTIONS */}
        <div className="form-actions" style={{ marginTop: "1.2rem" }}>
          <button
            className="btn-secondary"
            type="button"
            onClick={handleBack}
            disabled={checkoutLoading}
          >
            {t("common.back", "Back")}
          </button>
          <button
            className="btn-primary"
            type="button"
            onClick={handleActivateOrChangePlan}
            disabled={checkoutLoading || !selectedPlan}
          >
            {checkoutLoading
              ? isActive
                ? t("botPlan.actions.updating", "Updating")
                : t("botPlan.actions.redirecting", "Redirecting")
              : isActive
              ? t("botPlan.actions.changePlan", "Change plan")
              : t("botPlan.actions.activatePay", "Activate & pay")}
          </button>
        </div>
      </div>
    </OnboardingLayout>
  );
};

export default OnboardingPlanPage;
