// src/pages/onboarding/OnboardingBookingPage.tsx
import React, { useEffect, useState } from "react";
import { useParams, useNavigate } from "react-router-dom";
import { useTranslation } from "react-i18next";
import OnboardingLayout from "./OnboardingLayout";
import {
  Bot,
  getBotById,
  updateBot,
  BookingWeeklySchedule,
  Weekday
} from "../../api/bots";


import {
  ALWAYS_ON_FEATURES
} from "./onboardingFeatureHelpers";

const WEEKDAYS_ORDER: Weekday[] = [
  "monday",
  "tuesday",
  "wednesday",
  "thursday",
  "friday",
  "saturday",
  "sunday"
];

interface OpeningWindowForm {
  start: string;
  end: string;
}

interface DayScheduleForm {
  enabled: boolean;
  windows: OpeningWindowForm[];
}

type WeeklyScheduleForm = Record<Weekday, DayScheduleForm>;

interface BookingOnboardingForm {
  // Calendar basics
  calendarId: string;
  timeZone: string;
  defaultDurationMinutes: string;

  // Booking rules
  bookingMinLeadHours: string;
  bookingMaxAdvanceDays: string;
  bookingMaxSimultaneousBookings: string;

  // Weekly schedule
  bookingWeeklySchedule: WeeklyScheduleForm;
}

const buildEmptyWeeklySchedule = (): WeeklyScheduleForm => {
  const schedule: Partial<WeeklyScheduleForm> = {};
  WEEKDAYS_ORDER.forEach((day) => {
    schedule[day] = {
      enabled: false,
      windows: [{ start: "", end: "" }]
    };
  });
  return schedule as WeeklyScheduleForm;
};

const fromApiSchedule = (
  apiSchedule: BookingWeeklySchedule | null | undefined
): WeeklyScheduleForm => {
  const base = buildEmptyWeeklySchedule();

  if (!apiSchedule) {
    return base;
  }

  WEEKDAYS_ORDER.forEach((day) => {
    const windows = apiSchedule[day];
    if (windows && windows.length > 0) {
      base[day] = {
        enabled: true,
        windows: windows.map((w) => ({
          start: w.start,
          end: w.end
        }))
      };
    }
  });

  return base;
};

const normalizeWeeklyScheduleForApi = (
  formSchedule: WeeklyScheduleForm
): BookingWeeklySchedule | null => {
  const result: BookingWeeklySchedule = {};

  WEEKDAYS_ORDER.forEach((day) => {
    const dayForm = formSchedule[day];
    if (!dayForm.enabled) {
      return;
    }

    const validWindows: OpeningWindowForm[] = [];
    dayForm.windows.forEach((w) => {
      const start = w.start.trim();
      const end = w.end.trim();
      if (!start || !end) return;
      if (start >= end) return; // ignore invalid ranges
      validWindows.push({ start, end });
    });

    if (validWindows.length > 0) {
      result[day] = validWindows.map((w) => ({
        start: w.start,
        end: w.end
      }));
    }
  });

  return Object.keys(result).length > 0 ? result : null;
};

const coerceNullableNumber = (value: string): number | null => {
  const trimmed = value.trim();
  if (!trimmed) return null;
  const parsed = Number(trimmed);
  if (!Number.isFinite(parsed) || parsed < 0) return null;
  return parsed;
};

const OnboardingBookingPage: React.FC = () => {
  const { id } = useParams<{ id: string }>();
  const { t } = useTranslation();
  const navigate = useNavigate();

  const [bot, setBot] = useState<Bot | null>(null);
  const [form, setForm] = useState<BookingOnboardingForm | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [saving, setSaving] = useState<boolean>(false);
  const [error, setError] = useState<string | null>(null);
  const [activeDay, setActiveDay] = useState<Weekday>("monday");

  const [bookingEnabled, setBookingEnabled] = useState<boolean>(!!bot?.useCalendar);

  useEffect(() => {
    if (!id) {
      setLoading(false);
      return;
    }

    setLoading(true);
    setError(null);

    getBotById(id)
      .then((botData) => {
        setBot(botData);

        const toStringOrEmpty = (value: number | null | undefined): string =>
          typeof value === "number" ? String(value) : "";

        const weeklySchedule = fromApiSchedule(
          botData.bookingWeeklySchedule ?? null
        );

        setForm({
          calendarId: botData.calendarId || "",
          timeZone: botData.timeZone || "",
          defaultDurationMinutes: botData.defaultDurationMinutes
            ? String(botData.defaultDurationMinutes)
            : "30",
          bookingMinLeadHours: toStringOrEmpty(botData.bookingMinLeadHours),
          bookingMaxAdvanceDays: toStringOrEmpty(
            botData.bookingMaxAdvanceDays
          ),
          bookingMaxSimultaneousBookings: toStringOrEmpty(
            botData.bookingMaxSimultaneousBookings
          ),
          bookingWeeklySchedule: weeklySchedule
        });
        const firstEnabled =
          WEEKDAYS_ORDER.find((d) => weeklySchedule[d].enabled) ?? "monday";
        setActiveDay(firstEnabled);
      })
      .catch((err: any) => {
        console.error(err);
        setError(err?.message || t("botSettings.errors.loadFailed"));
      })
      .finally(() => setLoading(false));
  }, [id, t]);

  const handleStringChange =
    (field: keyof BookingOnboardingForm) =>
    (e: React.ChangeEvent<HTMLInputElement>) => {
      if (!form) return;
      setForm({ ...form, [field]: e.target.value });
    };

  const handleDayEnabledChange = (day: Weekday, enabled: boolean): void => {
    if (!form) return;
    setForm({
      ...form,
      bookingWeeklySchedule: {
        ...form.bookingWeeklySchedule,
        [day]: {
          ...form.bookingWeeklySchedule[day],
          enabled
        }
      }
    });
  };

  const handleWindowChange = (
    day: Weekday,
    index: number,
    field: "start" | "end",
    value: string
  ): void => {
    if (!form) return;
    const daySchedule = form.bookingWeeklySchedule[day];
    const windows = [...daySchedule.windows];
    windows[index] = {
      ...windows[index],
      [field]: value
    };
    setForm({
      ...form,
      bookingWeeklySchedule: {
        ...form.bookingWeeklySchedule,
        [day]: {
          ...daySchedule,
          windows
        }
      }
    });
  };

  const handleAddWindow = (day: Weekday): void => {
    if (!form) return;
    const daySchedule = form.bookingWeeklySchedule[day];
    setForm({
      ...form,
      bookingWeeklySchedule: {
        ...form.bookingWeeklySchedule,
        [day]: {
          ...daySchedule,
          windows: [...daySchedule.windows, { start: "", end: "" }]
        }
      }
    });
  };

  const handleRemoveWindow = (day: Weekday, index: number): void => {
    if (!form) return;
    const daySchedule = form.bookingWeeklySchedule[day];
    const windows = daySchedule.windows.filter((_, i) => i !== index);
    setForm({
      ...form,
      bookingWeeklySchedule: {
        ...form.bookingWeeklySchedule,
        [day]: {
          ...daySchedule,
          windows: windows.length > 0 ? windows : [{ start: "", end: "" }]
        }
      }
    });
  };

  const handleBack = () => {
    navigate(-1);
  };

  const handleSubmit: React.FormEventHandler = async (e) => {
    e.preventDefault();
    if (!id || !form) return;

    setSaving(true);
    setError(null);

    try {
      const parsedDuration = parseInt(form.defaultDurationMinutes, 10);
      const defaultDurationMinutes =
        Number.isFinite(parsedDuration) && parsedDuration > 0
          ? parsedDuration
          : 30;

      const bookingWeeklySchedule = normalizeWeeklyScheduleForApi(
        form.bookingWeeklySchedule
      );

      await updateBot(id, {
        calendarId: form.calendarId.trim() || null,
        timeZone: form.timeZone.trim() || null,
        defaultDurationMinutes,
        bookingMinLeadHours: coerceNullableNumber(form.bookingMinLeadHours),
        bookingMaxAdvanceDays: coerceNullableNumber(
          form.bookingMaxAdvanceDays
        ),
        bookingMaxSimultaneousBookings: coerceNullableNumber(
          form.bookingMaxSimultaneousBookings
        ),
        bookingWeeklySchedule,
        // FEATURE FLAG:
      useCalendar: bookingEnabled,
      // keep always-on features
      ...ALWAYS_ON_FEATURES
      });

      navigate(`/onboarding/bots/${encodeURIComponent(id)}/lead-ads`);
    } catch (err: any) {
      console.error(err);
      setError(err?.message || t("botSettings.errors.saveFailed"));
    } finally {
      setSaving(false);
    }
  };


  const handleSkip = async () => {
  if (!id) return;
  setSaving(true);
  setError(null);
  try {
    await updateBot(id, {
      ...ALWAYS_ON_FEATURES,
      useCalendar: false
    });
    navigate(`/onboarding/bots/${encodeURIComponent(id)}/lead-ads`);
  } catch (err: any) {
    console.error(err);
    setError(err?.message || t("common.error", "Something went wrong"));
  } finally {
    setSaving(false);
  }
};


  const renderContent = () => {
    if (!id) {
      return (
        <p className="muted">
          {t("botSettings.missingId", "Missing bot id in URL.")}
        </p>
      );
    }

    if (loading || !form) {
      return <p className="muted">{t("botSettings.loading", "Loading")}</p>;
    }

    return (
      <form className="form booking-form" onSubmit={handleSubmit}>
        {error && <div className="form-error">{error}</div>}

        {/* Top grid: calendar basics + rules */}
        <div className="booking-grid">
          <section className="booking-section">
            <h2 className="booking-section-title">
              {t(
                "botSettings.sections.calendarConnection.title",
                "Connect your calendar"
              )}
            </h2>
            <p className="booking-section-subtitle">
              {t(
                "botSettings.sections.calendarConnection.hint",
                "Tell Coslo which calendar to use for availability and bookings."
              )}
            </p>

            <div className="booking-fields-grid">
              <label className="form-field">
                <span>{t("botSettings.fields.calendarId.label")}</span>
                <input
                  type="text"
                  value={form.calendarId}
                  onChange={handleStringChange("calendarId")}
                  placeholder={t(
                    "botSettings.fields.calendarId.placeholder",
                    "e.g. primary or a specific calendar id"
                  )}
                />
                <span className="bot-settings-help">
                  {t("botSettings.fields.calendarId.help")} <code>primary</code>
                </span>
              </label>

              <label className="form-field">
                <span>{t("botSettings.fields.timeZone.label")}</span>
                <input
                  type="text"
                  value={form.timeZone}
                  onChange={handleStringChange("timeZone")}
                  placeholder={t(
                    "botSettings.fields.timeZone.placeholder",
                    "e.g. Europe/Rome"
                  )}
                />
                <span className="bot-settings-help">
                  {t("botSettings.fields.timeZone.help")}
                </span>
              </label>

              <label className="form-field">
                <span>
                  {t("botSettings.fields.defaultDurationMinutes.label")}
                </span>
                <input
                  type="number"
                  min={5}
                  max={480}
                  value={form.defaultDurationMinutes}
                  onChange={handleStringChange("defaultDurationMinutes")}
                  placeholder={t(
                    "botSettings.fields.defaultDurationMinutes.placeholder",
                    "30"
                  )}
                />
                <span className="bot-settings-help">
                  {t("botSettings.fields.defaultDurationMinutes.help")}
                </span>
              </label>
            </div>
          </section>

          <section className="booking-section">
            <h2 className="booking-section-title">
              {t(
                "botSettings.sections.bookingRules.title",
                "Booking rules"
              )}
            </h2>
            <p className="booking-section-subtitle">
              {t(
                "botSettings.sections.bookingRules.hint",
                "Control how far in advance users can book and how many bookings can overlap."
              )}
            </p>

            <div className="booking-fields-grid">
              <label className="form-field">
                <span>
                  {t("botSettings.fields.bookingMinLeadHours.label")}
                </span>
                <input
                  type="number"
                  min={0}
                  step={1}
                  value={form.bookingMinLeadHours}
                  onChange={handleStringChange("bookingMinLeadHours")}
                  placeholder={t(
                    "botSettings.fields.bookingMinLeadHours.placeholder"
                  )}
                />
                <span className="bot-settings-help">
                  {t("botSettings.fields.bookingMinLeadHours.help")}
                </span>
              </label>

              <label className="form-field">
                <span>
                  {t("botSettings.fields.bookingMaxAdvanceDays.label")}
                </span>
                <input
                  type="number"
                  min={0}
                  step={1}
                  value={form.bookingMaxAdvanceDays}
                  onChange={handleStringChange("bookingMaxAdvanceDays")}
                  placeholder={t(
                    "botSettings.fields.bookingMaxAdvanceDays.placeholder"
                  )}
                />
                <span className="bot-settings-help">
                  {t("botSettings.fields.bookingMaxAdvanceDays.help")}
                </span>
              </label>

              <label className="form-field">
                <span>
                  {t(
                    "botSettings.fields.bookingMaxSimultaneousBookings.label"
                  )}
                </span>
                <input
                  type="number"
                  min={1}
                  value={form.bookingMaxSimultaneousBookings}
                  onChange={handleStringChange(
                    "bookingMaxSimultaneousBookings"
                  )}
                  placeholder={t(
                    "botSettings.fields.bookingMaxSimultaneousBookings.placeholder"
                  )}
                />
                <span className="bot-settings-help">
                  {t(
                    "botSettings.fields.bookingMaxSimultaneousBookings.help"
                  )}
                </span>
              </label>
            </div>
          </section>
        </div>

        {/* Availability */}
{/* Availability */}
        <section className="booking-section booking-availability">
          <h2 className="booking-section-title">
            {t(
              "botSettings.sections.availability.title",
              "Weekly availability"
            )}
          </h2>
          <p className="booking-section-subtitle">
            {t(
              "botSettings.sections.availability.hint",
              "Choose on which days and time ranges Coslo can schedule meetings for this bot."
            )}
          </p>

          {/* Day pills */}
          <div className="booking-day-tabs">
            {WEEKDAYS_ORDER.map((dayKey) => {
              const dayForm = form.bookingWeeklySchedule[dayKey];
              const isActive = activeDay === dayKey;
              const isEnabled = dayForm.enabled;

              const className = [
                "booking-day-pill",
                isActive && "booking-day-pill--active"
              ]
                .filter(Boolean)
                .join(" ");

              return (
                <button
                  key={dayKey}
                  type="button"
                  className={className}
                  onClick={() => setActiveDay(dayKey)}
                >
                  <span className="booking-day-pill-label">
                    {t(`botSettings.weekdays.${dayKey}`)}
                  </span>
                  <span
                    className={
                      "booking-day-pill-dot" +
                      (isEnabled ? " booking-day-pill-dot--on" : "")
                    }
                    aria-hidden="true"
                  />
                </button>
              );
            })}
          </div>

          {/* Selected day details */}
          {(() => {
            const day = activeDay;
            const dayForm = form.bookingWeeklySchedule[day];

            return (
              <div className="booking-day-detail-card">
                <div className="booking-day-detail-header">
                  <div>
                    <h3 className="booking-day-detail-title">
                      {t(`botSettings.weekdays.${day}`)}
                    </h3>
                    <p className="booking-day-detail-subtitle">
                      {dayForm.enabled
                        ? t(
                            "botSettings.sections.availability.enabledHint",
                            "Bookings are allowed on this day within the time ranges below."
                          )
                        : t(
                            "botSettings.sections.availability.disabledHint",
                            "Bookings are not allowed on this day until you enable it."
                          )}
                    </p>
                  </div>

                  <label className="booking-toggle">
                    <input
                      type="checkbox"
                      checked={dayForm.enabled}
                      onChange={(e) =>
                        handleDayEnabledChange(day, e.target.checked)
                      }
                    />
                    <span className="booking-toggle-slider" />
                    <span className="booking-toggle-label">
                      {t(
                        "botSettings.sections.availability.acceptBookings",
                        "Accept bookings this day"
                      )}
                    </span>
                  </label>
                </div>

                <div className="booking-day-ranges">
                  {dayForm.windows.map((window, idx) => (
                    <div key={idx} className="booking-day-range-row">
                      <div className="booking-day-range-inputs">
                        <label>
                          <span className="booking-range-label">
                            {t(
                              "botSettings.sections.availability.from",
                              "From"
                            )}
                          </span>
                          <input
                            type="time"
                            value={window.start}
                            onChange={(e) =>
                              handleWindowChange(
                                day,
                                idx,
                                "start",
                                e.target.value
                              )
                            }
                          />
                        </label>

                        <label>
                          <span className="booking-range-label">
                            {t(
                              "botSettings.sections.availability.to",
                              "To"
                            )}
                          </span>
                          <input
                            type="time"
                            value={window.end}
                            onChange={(e) =>
                              handleWindowChange(
                                day,
                                idx,
                                "end",
                                e.target.value
                              )
                            }
                          />
                        </label>
                      </div>

                      {idx > 0 && (
                        <button
                          type="button"
                          className="btn-link booking-remove-range"
                          onClick={() => handleRemoveWindow(day, idx)}
                        >
                          {t(
                            "botSettings.sections.availability.removeRange",
                            "Remove range"
                          )}
                        </button>
                      )}
                    </div>
                  ))}
                </div>

                <button
                  type="button"
                  className="btn-secondary btn-xs booking-add-range"
                  onClick={() => handleAddWindow(day)}
                >
                  {t(
                    "botSettings.sections.availability.addRange",
                    "Add time range"
                  )}
                </button>

                <p className="bot-settings-hint booking-availability-note">
                  {t("botSettings.sections.availability.note")}
                </p>
              </div>
            );
          })()}
        </section>

        <div className="form-actions booking-actions">
          <button
            className="btn-secondary"
            type="button"
            onClick={handleBack}
            disabled={saving}
          >
            {t("common.back", "Back")}
          </button>
          <button className="btn-primary" type="submit" disabled={saving}>
            {saving
              ? t("botSettings.actions.saving", "Saving")
              : t(
                  "onboarding.booking.next",
                  "Save & continue to Lead Ads"
                )}
          </button>
        </div>
      </form>
    );
  };

  return (
    <OnboardingLayout
      currentStep="booking"
      botId={id}
      title={t("onboarding.booking.title", "Enable bookings from chat")}
      subtitle={t(
        "onboarding.booking.subtitle",
        "Connect your calendar, define your available slots and booking rules."
      )}
      layout="full"
      rightAction={
        <button
          type="button"
          className="lp-btn lp-btn-ghost lp-btn-sm"
          onClick={handleSkip}
          disabled={saving}
        >
          {t("onboarding.skip", "Skip for now")}
        </button>
  }
    >
      <div className="onboarding-booking">{renderContent()}</div>
    </OnboardingLayout>
  );
};

export default OnboardingBookingPage;
