// src/pages/app/DashboardPage.tsx
import React, { useEffect, useMemo, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  Legend,
  CartesianGrid,
  BarChart,
  Bar
} from "recharts";
import { Link } from "react-router-dom";
import {
  fetchDashboardOverview,
  DashboardOverviewResponse,
  TopBotActivity
} from "../../api/dashboard";

const LINE_COLORS = [
  "#6366F1", // indigo
  "#EC4899", // pink
  "#10B981", // green
  "#F59E0B", // amber
  "#3B82F6", // blue
  "#8B5CF6", // violet
  "#F97316", // orange
  "#22C55E" // emerald
];

function getLocaleFromLanguage(language: string | undefined): string {
  const code = (language || "en").split("-")[0];
  switch (code) {
    case "it":
      return "it-IT";
    case "es":
      return "es-ES";
    default:
      return "en-GB";
  }
}

const DashboardPage: React.FC = () => {
  const { t, i18n } = useTranslation();
  const locale = useMemo(
    () => getLocaleFromLanguage(i18n.resolvedLanguage || i18n.language),
    [i18n.language, i18n.resolvedLanguage]
  );

  const [data, setData] = useState<DashboardOverviewResponse | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    let mounted = true;

    const load = async () => {
      try {
        const res = await fetchDashboardOverview();
        if (mounted) setData(res);
      } catch (err: any) {
        console.error("Failed to load dashboard", err);
        if (mounted) setError(err?.message || t("dashboard.errors.loadFailed"));
      } finally {
        if (mounted) setLoading(false);
      }
    };

    load();
    return () => {
      mounted = false;
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const hasBots = (data?.kpis.totalBots ?? 0) > 0;
  const hasConvSeries = (data?.conversationsLast10Days.series.length ?? 0) > 0;
  const hasTokenSeries = (data?.tokensLast10Days.series.length ?? 0) > 0;
  const hasTopBots = (data?.topBotsByConversationsLast30Days.length ?? 0) > 0;

  const hasTokenSplit =
    data?.tokenBreakdownLast10Days?.dates.length ? true : false;

  const tOr = (key: string, fallback: string): string => {
    const anyI18n = i18n as any;
    if (typeof anyI18n?.exists === "function" && anyI18n.exists(key)) {
      return t(key);
    }
    return fallback;
  };

  const formatPct = (v: number | null | undefined): string => {
    if (v == null || Number.isNaN(v)) return "";
    return `${v.toFixed(1)}%`;
  };

  // UI-only: show tokens/10 (raw stays in API + backend math)
  const tokensThisMonthUi = data
    ? Math.round(data.kpis.totalTokensThisMonth / 10)
    : 0;
  const tokensLimitUi =
    data?.kpis.monthlyTokensLimit == null
      ? null
      : Math.round(data.kpis.monthlyTokensLimit / 10);

  const tokensPlanHint = useMemo(() => {
    if (!data) return "";
    if (data.kpis.monthlyTokensLimit == null) {
      return tOr("dashboard.kpisHints.unlimitedPlan", "Unlimited plan");
    }
    if (data.kpis.monthlyTokensLimit <= 0) return "";
    const limit = tokensLimitUi ?? Math.round(data.kpis.monthlyTokensLimit / 10);
    return `${tOr("dashboard.kpisHints.of", "of")} ${limit.toLocaleString(
      locale
    )}`;
  }, [data, locale, tokensLimitUi, i18n, t]);

  const emailsPlanHint = useMemo(() => {
    if (!data) return "";
    if (data.kpis.monthlyEmailsLimit == null) {
      return tOr("dashboard.kpisHints.unlimitedPlan", "Unlimited plan");
    }
    if (data.kpis.monthlyEmailsLimit <= 0) return "";
    return `${tOr(
      "dashboard.kpisHints.of",
      "of"
    )} ${data.kpis.monthlyEmailsLimit.toLocaleString(locale)}`;
  }, [data, locale, i18n, t]);

  // Helpers for token UI formatting (keep raw numbers under the hood)
  const formatTokenTick = (value: number): string =>
    Math.round(value / 10).toLocaleString(locale);

  const formatTokenTooltipValue = (
    value: number | string,
    name: string
  ): [string, string] => {
    const num = typeof value === "number" ? value : Number(value) || 0;
    const ui = Math.round(num / 10).toLocaleString(locale);
    return [ui, name];
  };

  // ---- Chart data transforms ----
  const conversationsChartData = useMemo(() => {
    if (!data || !hasConvSeries) return [];
    const { dates, series } = data.conversationsLast10Days;

    return dates.map((date, idx) => {
      const entry: Record<string, any> = { date };
      series.forEach((s) => {
        entry[s.botName] = s.values[idx] ?? 0;
      });
      return entry;
    });
  }, [data, hasConvSeries]);

  const tokensChartData = useMemo(() => {
    if (!data || !hasTokenSeries) return [];
    const { dates, series } = data.tokensLast10Days;

    // IMPORTANT: keep raw values here, only format in axis/tooltip
    return dates.map((date, idx) => {
      const entry: Record<string, any> = { date };
      series.forEach((s) => {
        entry[s.botName] = s.values[idx] ?? 0;
      });
      return entry;
    });
  }, [data, hasTokenSeries]);

  const tokenSplitChartData = useMemo(() => {
    if (!data || !hasTokenSplit) return [];
    const { dates, openAiTokens, emailTokens, whatsappTokens } =
      data.tokenBreakdownLast10Days;

    // IMPORTANT: keep raw token values, format in axis/tooltip
    return dates.map((date, idx) => ({
      date,
      openAi: openAiTokens[idx] ?? 0,
      emails: emailTokens[idx] ?? 0,
      whatsapp: whatsappTokens[idx] ?? 0
    }));
  }, [data, hasTokenSplit]);

  const topBotsChartData = useMemo(() => {
    if (!data || !hasTopBots) return [];
    return data.topBotsByConversationsLast30Days.map((b: TopBotActivity) => ({
      name: b.botName,
      conversations: b.conversationCount
    }));
  }, [data, hasTopBots]);

  return (
    <div className="page-container dashboard-page">
      <div className="page-header">
        <div>
          <h1 className="page-title">{t("dashboard.title")}</h1>
          <p className="page-subtitle">{t("dashboard.subtitle")}</p>
        </div>

        {hasBots && (
          <Link to="/app/bots/new" className="btn-primary">
            {t("dashboard.actions.createNewBot")}
          </Link>
        )}
      </div>

      {loading && (
        <div className="card">
          <p>{t("dashboard.loading")}</p>
        </div>
      )}

      {!loading && error && (
        <div className="card card-error">
          <p>{error}</p>
        </div>
      )}

      {!loading && !error && !hasBots && (
        <div className="card">
          <h2 className="card-title">{t("dashboard.empty.title")}</h2>
          <p className="card-subtitle">{t("dashboard.empty.subtitle")}</p>
          <div style={{ marginTop: "1rem" }}>
            <Link to="/app/bots/new" className="btn-primary">
              {t("dashboard.empty.cta")}
            </Link>
          </div>
        </div>
      )}

      {!loading && !error && hasBots && data && (
        <>
          {/* KPI cards */}
          <section className="dashboard-section">
            {/* Row 1 (3 cards) */}
            <div className="dashboard-kpi-row dashboard-kpi-row--top">
              <div className="dashboard-kpi-card">
                <div className="dashboard-kpi-label">
                  {t("dashboard.kpis.totalBots")}
                </div>
                <div className="dashboard-kpi-value">
                  {data.kpis.totalBots}
                </div>
                <div className="dashboard-kpi-hint">
                  {t("dashboard.kpisHints.totalBots")}
                </div>
              </div>

              <div className="dashboard-kpi-card">
                <div className="dashboard-kpi-label">
                  {t("dashboard.kpis.activeBots")}
                </div>
                <div className="dashboard-kpi-value">
                  {data.kpis.activeBots}
                </div>
                <div className="dashboard-kpi-hint">
                  {t("dashboard.kpisHints.activeBots")}
                </div>
              </div>

              <div className="dashboard-kpi-card">
                <div className="dashboard-kpi-label">
                  {t("dashboard.kpis.conversationsLast30Days")}
                </div>
                <div className="dashboard-kpi-value">
                  {data.kpis.totalConversationsLast30Days}
                </div>
                <div className="dashboard-kpi-hint">
                  {t("dashboard.kpisHints.conversationsLast30Days")}
                </div>
              </div>
              <div className="dashboard-kpi-card">
                <div className="dashboard-kpi-label">
                  {t("dashboard.kpis.tokensThisMonth")}
                </div>

                <div className="dashboard-kpi-value-row">
                  <div className="dashboard-kpi-value">
                    {tokensThisMonthUi.toLocaleString(locale)}
                  </div>
                  {data.kpis.tokensUsagePercent != null && (
                    <span className="dashboard-kpi-pill">
                      {formatPct(data.kpis.tokensUsagePercent)}
                    </span>
                  )}
                </div>

                <div className="dashboard-kpi-hint">{tokensPlanHint}</div>
              </div>
            </div>
          </section>

          {/* Conversations / Tokens charts */}
          <section className="dashboard-section">
            <div className="dashboard-grid">
              <div className="card chart-card">
                <div className="card-header">
                  <h2>
                    {t("dashboard.charts.conversationsLast10Days.title")}
                  </h2>
                  <p className="card-subtitle">
                    {t("dashboard.charts.conversationsLast10Days.subtitle")}
                  </p>
                </div>

                {hasConvSeries ? (
                  <div style={{ width: "100%", height: 320 }}>
                    <ResponsiveContainer>
                      <LineChart data={conversationsChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="date" />
                        <YAxis allowDecimals={false} />
                        <Tooltip
                          contentStyle={{
                            background: "rgba(17,24,39,0.95)",
                            border: "1px solid rgba(255,255,255,0.08)",
                            borderRadius: 10
                          }}
                          labelStyle={{ color: "#9ca3af" }}
                        />
                        <Legend />
                        {data.conversationsLast10Days.series.map(
                          (s, idx) => (
                            <Line
                              key={s.botId}
                              type="monotone"
                              dataKey={s.botName}
                              stroke={
                                LINE_COLORS[idx % LINE_COLORS.length]
                              }
                              strokeWidth={2}
                              dot={false}
                            />
                          )
                        )}
                      </LineChart>
                    </ResponsiveContainer>
                  </div>
                ) : (
                  <p className="card-empty">
                    {t(
                      "dashboard.charts.conversationsLast10Days.empty"
                    )}
                  </p>
                )}
              </div>

              <div className="card chart-card">
                <div className="card-header">
                  <h2>{t("dashboard.charts.tokensLast10Days.title")}</h2>
                  <p className="card-subtitle">
                    {t("dashboard.charts.tokensLast10Days.subtitle")}
                  </p>
                </div>

                {hasTokenSeries ? (
                  <div style={{ width: "100%", height: 320 }}>
                    <ResponsiveContainer>
                      <LineChart data={tokensChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="date" />
                        <YAxis
                          allowDecimals={false}
                          tickFormatter={formatTokenTick}
                        />
                        <Tooltip
                          contentStyle={{
                            background: "rgba(17,24,39,0.95)",
                            border: "1px solid rgba(255,255,255,0.08)",
                            borderRadius: 10
                          }}
                          labelStyle={{ color: "#9ca3af" }}
                          formatter={formatTokenTooltipValue}
                        />
                        <Legend />
                        {data.tokensLast10Days.series.map((s, idx) => (
                          <Line
                            key={s.botId}
                            type="monotone"
                            dataKey={s.botName}
                            stroke={
                              LINE_COLORS[idx % LINE_COLORS.length]
                            }
                            strokeWidth={2}
                            dot={false}
                          />
                        ))}
                      </LineChart>
                    </ResponsiveContainer>
                  </div>
                ) : (
                  <p className="card-empty">
                    {t("dashboard.charts.tokensLast10Days.empty")}
                  </p>
                )}
              </div>

              <div className="card chart-card">
                <div className="card-header">
                  <h2>
                    {tOr(
                      "dashboard.charts.tokensSplitLast10Days.title",
                      "Tokens by channel (last 10 days)"
                    )}
                  </h2>
                  <p className="card-subtitle">
                    {tOr(
                      "dashboard.charts.tokensSplitLast10Days.subtitle",
                      "OpenAI + knowledge vs emails vs WhatsApp templates"
                    )}
                  </p>
                </div>

                {hasTokenSplit ? (
                  <div style={{ width: "100%", height: 320 }}>
                    <ResponsiveContainer>
                      <BarChart data={tokenSplitChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="date" />
                        <YAxis
                          allowDecimals={false}
                          tickFormatter={formatTokenTick}
                        />
                        <Tooltip
                          contentStyle={{
                            background: "rgba(17,24,39,0.95)",
                            border: "1px solid rgba(255,255,255,0.08)",
                            borderRadius: 10,
                            padding: "0.45rem 0.7rem",
                            fontSize: "0.8rem"
                          }}
                          labelStyle={{ color: "#9ca3af" }}
                          formatter={formatTokenTooltipValue}
                        />
                        <Legend />
                        {/* OpenAI + Knowledge */}
                        <Bar
                          dataKey="openAi"
                          stackId="tokens"
                          fill="#6366F1"
                          name={tOr(
                            "dashboard.tokensSplit.legend.openAi",
                            "OpenAI + knowledge"
                          )}
                          radius={[6, 6, 0, 0]}
                        />
                        {/* Emails (virtual tokens) */}
                        <Bar
                          dataKey="emails"
                          stackId="tokens"
                          fill="#F97316"
                          name={tOr(
                            "dashboard.tokensSplit.legend.emails",
                            "Emails"
                          )}
                        />
                        {/* WhatsApp lead templates */}
                        <Bar
                          dataKey="whatsapp"
                          stackId="tokens"
                          fill="#22C55E"
                          name={tOr(
                            "dashboard.tokensSplit.legend.whatsapp",
                            "WhatsApp templates"
                          )}
                          radius={[0, 0, 6, 6]}
                        />
                      </BarChart>
                    </ResponsiveContainer>
                  </div>
                ) : (
                  <p className="card-empty">
                    {tOr(
                      "dashboard.charts.tokensSplitLast10Days.empty",
                      "Not enough data yet to show the split."
                    )}
                  </p>
                )}
              </div>

              <div className="card chart-card">
                <div className="card-header">
                  <h2>{t("dashboard.charts.topBots.title")}</h2>
                  <p className="card-subtitle">
                    {t("dashboard.charts.topBots.subtitle")}
                  </p>
                </div>

                {hasTopBots ? (
                  <div style={{ width: "100%", height: 320 }}>
                    <ResponsiveContainer>
                      <BarChart data={topBotsChartData}>
                        <CartesianGrid strokeDasharray="3 3" />
                        <XAxis dataKey="name" />
                        <YAxis allowDecimals={false} />
                        <Tooltip
                          contentStyle={{
                            background: "rgba(17,24,39,0.95)",
                            border: "1px solid rgba(255,255,255,0.08)",
                            borderRadius: 10,
                            padding: "0.45rem 0.7rem",
                            fontSize: "0.8rem"
                          }}
                          labelStyle={{ color: "#9ca3af" }}
                        />
                        <Bar
                          dataKey="conversations"
                          fill="#6366F1"
                          radius={[6, 6, 0, 0]}
                        />
                      </BarChart>
                    </ResponsiveContainer>
                  </div>
                ) : (
                  <p className="card-empty">
                    {t("dashboard.charts.topBots.empty")}
                  </p>
                )}
              </div>

              <div className="card chart-card">
                <div className="card-header">
                  <h2>{t("dashboard.activity.title")}</h2>
                  <p className="card-subtitle">
                    {t("dashboard.activity.subtitle")}
                  </p>
                </div>

                {hasTopBots ? (
                  <ul className="list-plain">
                    {data.topBotsByConversationsLast30Days.map((bot) => (
                      <li key={bot.botId} className="list-row">
                        <div className="list-row-main">
                          <div className="list-title">{bot.botName}</div>
                          {bot.lastConversationAt && (
                            <div className="list-subtitle">
                              {t(
                                "dashboard.activity.lastConversation"
                              )}{" "}
                              {new Date(
                                bot.lastConversationAt
                              ).toLocaleString(locale)}
                            </div>
                          )}
                        </div>

                        <div className="list-row-meta">
                          <span className="badge">
                            {bot.conversationCount}{" "}
                            {t("dashboard.activity.convAbbrev")}
                          </span>
                          <Link
                            to={`/app/bots/${bot.botId}/conversations`}
                            className="btn-link-small"
                          >
                            {t("dashboard.activity.view")}
                          </Link>
                        </div>
                      </li>
                    ))}
                  </ul>
                ) : (
                  <p className="card-empty">
                    {t("dashboard.activity.empty")}
                  </p>
                )}
              </div>
            </div>
          </section>
        </>
      )}
    </div>
  );
};

export default DashboardPage;
