// src/pages/onboarding/OnboardingLeadAdsPage.tsx
import React, { useEffect, useMemo, useState } from "react";
import { useParams, useNavigate, Link } from "react-router-dom";
import { useTranslation } from "react-i18next";

import OnboardingLayout from "./OnboardingLayout";

import {
  Bot,
  BotChannel,
  getBotById,
  fetchChannels,
  fetchWhatsappTemplates,
  fetchMetaLeadAutomation,
  upsertMetaLeadAutomation,
  MetaLeadAutomationSettings,
  WhatsappTemplate,
  updateBot
} from "../../api/bots";

const OnboardingLeadAdsPage: React.FC = () => {
  const { id: botId } = useParams<{ id: string }>();
  const { t } = useTranslation();
  const navigate = useNavigate();

  const [bot, setBot] = useState<Bot | null>(null);
  const [channels, setChannels] = useState<BotChannel[]>([]);
  const [templates, setTemplates] = useState<WhatsappTemplate[]>([]);

  const [loading, setLoading] = useState(true);
  const [templatesLoading, setTemplatesLoading] = useState(false);

  const [error, setError] = useState<string | null>(null);
  const [saving, setSaving] = useState(false);

  // Lead Ads  WhatsApp automation config
  const [leadConfigDraft, setLeadConfigDraft] =
    useState<MetaLeadAutomationSettings>({
      phoneFieldName: "phone_number",
      consentFieldName: "",
      requiresWhatsappOptIn: false,
      templateName: "",
      templateLanguage: ""
    } as MetaLeadAutomationSettings);

  // Whether the Lead Ads feature is ON in onboarding
  const [enabled, setEnabled] = useState<boolean>(false);

  useEffect(() => {
    if (!botId) return;

    const load = async () => {
      setLoading(true);
      setError(null);

      try {
        const [botRes, channelsRes, templatesRes, automationRes] =
          await Promise.all([
            getBotById(botId),
            fetchChannels(botId),
            fetchWhatsappTemplates(botId, { limit: 50 }),
            fetchMetaLeadAutomation(botId)
          ]);

        setBot(botRes);
        setChannels(channelsRes);
        setTemplates(templatesRes.items ?? templatesRes);

        if (automationRes) {
          setLeadConfigDraft({
            phoneFieldName: automationRes.phoneFieldName || "phone_number",
            consentFieldName: automationRes.consentFieldName || "",
            requiresWhatsappOptIn: automationRes.requiresWhatsappOptIn,
            templateName: automationRes.templateName || "",
            templateLanguage: automationRes.templateLanguage || ""
          });
        }

        // Initial ON/OFF state:
        // - ON if any lead Whatsapp feature flag is true
        // - otherwise ON if there is an automation with a template chosen
        const hasLeadFeature =
          !!botRes.leadWhatsappMessages200 ||
          !!botRes.leadWhatsappMessages500 ||
          !!botRes.leadWhatsappMessages1000;

        const hasAutomationTemplate =
          !!automationRes?.templateName && !!automationRes?.templateLanguage;

        setEnabled(hasLeadFeature || hasAutomationTemplate);
      } catch (err: any) {
        console.error("Failed to load Lead Ads onboarding", err);
        setError(
          err?.message ||
            t(
              "onboarding.leadAds.errors.load",
              "Could not load Lead Ads configuration."
            )
        );
      } finally {
        setLoading(false);
      }
    };

    void load();
  }, [botId, t]);

  const facebookChannel =
    channels.find((c) => c.type === "FACEBOOK") || null;
  const whatsappChannel =
    channels.find((c) => c.type === "WHATSAPP") || null;

  // For onboarding we just care that they exist; full status logic lives on Channels page
  const isFacebookConnected = !!facebookChannel;
  const isWhatsappConnected = !!whatsappChannel;
  const isReadyForAutomation = isFacebookConnected && isWhatsappConnected;

  const approvedTemplates = useMemo(
    () => templates.filter((tpl) => tpl.status === "APPROVED"),
    [templates]
  );

  const selectedTemplate: WhatsappTemplate | null = useMemo(() => {
    if (!leadConfigDraft.templateName || !leadConfigDraft.templateLanguage) {
      return null;
    }
    return (
      approvedTemplates.find(
        (tpl) =>
          tpl.name === leadConfigDraft.templateName &&
          tpl.language === leadConfigDraft.templateLanguage
      ) || null
    );
  }, [approvedTemplates, leadConfigDraft.templateName, leadConfigDraft.templateLanguage]);

  const formDisabled = !enabled || !isReadyForAutomation;

  const validateAutomation = (): string | null => {
    // If Lead Ads are OFF we don't validate anything
    if (!enabled) {
      return null;
    }

    if (!isReadyForAutomation) {
      return t(
        "onboarding.leadAds.errors.channelsNotReady",
        "To enable this automation, connect both Facebook and WhatsApp in the Channels step."
      );
    }

    if (!leadConfigDraft.phoneFieldName?.trim()) {
      return t(
        "whatsappTemplates.leadAutomation.errors.missingPhoneField",
        "Please specify the phone field name from your lead forms."
      );
    }

    if (
      leadConfigDraft.requiresWhatsappOptIn &&
      !leadConfigDraft.consentFieldName?.trim()
    ) {
      return t(
        "whatsappTemplates.leadAutomation.errors.missingConsentField",
        "If you require explicit opt-in, you must specify the consent field name."
      );
    }

    if (!leadConfigDraft.templateName || !leadConfigDraft.templateLanguage) {
      return t(
        "whatsappTemplates.leadAutomation.errors.missingTemplate",
        "Please select an approved WhatsApp template."
      );
    }

    return null;
  };

  const handleBack = () => {
    if (!botId) return;
    navigate(`/onboarding/bots/${encodeURIComponent(botId)}/booking`);
  };

  const handleSkip = async () => {
    if (!botId) return;

    // Skipping = feature OFF
    try {
      setSaving(true);
      setError(null);

      await updateBot(botId, {
        leadWhatsappMessages200: false,
        leadWhatsappMessages500: false,
        leadWhatsappMessages1000: false
      });

      navigate(`/onboarding/bots/${encodeURIComponent(botId)}/plan`);
    } catch (err: any) {
      console.error(err);
      setError(
        err?.message ||
          t(
            "onboarding.leadAds.errors.save",
            "Could not save Lead Ads configuration."
          )
      );
    } finally {
      setSaving(false);
    }
  };

  const handleNext = async () => {
    if (!botId) return;

    const validationError = validateAutomation();
    if (validationError) {
      setError(validationError);
      return;
    }

    setSaving(true);
    setError(null);

    try {
      if (enabled) {
        // Save automation config
        const payload: MetaLeadAutomationSettings = {
          phoneFieldName: leadConfigDraft.phoneFieldName || "phone_number",
          consentFieldName: leadConfigDraft.consentFieldName || "",
          requiresWhatsappOptIn: leadConfigDraft.requiresWhatsappOptIn,
          templateName: leadConfigDraft.templateName || "",
          templateLanguage: leadConfigDraft.templateLanguage || ""
        };

        await upsertMetaLeadAutomation(botId, payload);

        // Mark Lead Ads feature as enabled (minimal tier  you can adjust tier later on the plan)
        await updateBot(botId, {
          leadWhatsappMessages200: true,
          leadWhatsappMessages500: false,
          leadWhatsappMessages1000: false
        });
      } else {
        // Feature explicitly OFF
        await updateBot(botId, {
          leadWhatsappMessages200: false,
          leadWhatsappMessages500: false,
          leadWhatsappMessages1000: false
        });
      }

      navigate(`/onboarding/bots/${encodeURIComponent(botId)}/plan`);
    } catch (err: any) {
      console.error(err);
      setError(
        err?.message ||
          t(
            "onboarding.leadAds.errors.save",
            "Could not save Lead Ads configuration."
          )
      );
    } finally {
      setSaving(false);
    }
  };

  if (!botId) {
    return null;
  }

  if (loading) {
    return (
      <OnboardingLayout
        currentStep="leadAds"
        botId={botId}
        title={t("onboarding.leadAds.title", "Connect Facebook Lead Ads and WhatsApp automation")}
        subtitle={t(
          "onboarding.leadAds.subtitle",
          "Choose a WhatsApp template and map the fields from your Lead Ads forms."
        )}
      >
        <div className="card">
          <p className="muted">{t("common.loading", "Loading")}</p>
        </div>
      </OnboardingLayout>
    );
  }

  const title = t(
    "onboarding.leadAds.title",
    "Connect Facebook Lead Ads and WhatsApp automation"
  );
  const subtitle = t(
    "onboarding.leadAds.subtitle",
    "Choose a WhatsApp template and map the fields from your Lead Ads forms."
  );

  return (
    <OnboardingLayout
      currentStep="leadAds"
      botId={botId}
      title={title}
      subtitle={subtitle}
    >
      <div className="onboarding-section">

        <div className="onboarding-section-header">
          <div />
          <button
            type="button"
            className="btn-ghost btn-sm"
            onClick={handleSkip}
            disabled={saving}
          >
            {t("onboarding.common.skip", "Skip for now")}
          </button>
        </div>

        {error && (
          <div className="alert alert-error" style={{ marginBottom: "1rem" }}>
            <div>{error}</div>
          </div>
        )}

        {/* Connection status card */}
        <section className="lead-card lead-card--status">
          <header className="lead-card-header">
            <div>
              <h2>
                {t(
                  "onboarding.leadAds.connectionTitle",
                  "Connection status"
                )}
              </h2>
              <p className="lead-card-subtitle">
                {t(
                  "onboarding.leadAds.connectionSubtitle",
                  "Lead Ads automation requires both Facebook and WhatsApp channels."
                )}
              </p>
            </div>
            <div className="leadads-status-row">
              <span className="leadads-status-pill">
                <span className="leadads-status-dot leadads-status-dot--fb" />
                Facebook{" "}
                {isFacebookConnected
                  ? t("common.connected", "connected")
                  : t("common.notConnected", "not connected")}
              </span>
              <span className="leadads-status-pill">
                <span className="leadads-status-dot leadads-status-dot--wa" />
                WhatsApp{" "}
                {isWhatsappConnected
                  ? t("common.connected", "connected")
                  : t("common.notConnected", "not connected")}
              </span>
            </div>
          </header>

          {!isReadyForAutomation && (
            <p className="muted small">
              {t(
                "onboarding.leadAds.channelsHint",
                "To enable the automation, first connect both channels in the Channels step."
              )}{" "}
              <Link
                to={`/onboarding/bots/${encodeURIComponent(
                  botId
                )}/channels`}
              >
                {t("onboarding.leadAds.goToChannels", "Go to Channels")}
              </Link>
              .
            </p>
          )}
        </section>

        {/* Automation + template selection */}
        <section className="lead-card">
          <header className="lead-card-header">
            <div>
              <h2>
                {t(
                  "whatsappTemplates.leadAutomation.title",
                  "Lead Ads  WhatsApp automation"
                )}
              </h2>
              <p className="lead-card-subtitle">
                {t(
                  "onboarding.leadAds.automationHint",
                  "When a new lead arrives from Meta, Coslo will send a WhatsApp template and optionally check for explicit opt-in."
                )}
              </p>
            </div>

            <div className="leadads-toggle">
              <label className="booking-toggle">
                <input
                  type="checkbox"
                  checked={enabled}
                  disabled={!isReadyForAutomation || saving}
                  onChange={(e) => setEnabled(e.target.checked)}
                />
                <span className="booking-toggle-slider" />
                <span className="booking-toggle-label">
                  {enabled
                    ? t(
                        "onboarding.leadAds.toggleOn",
                        "Lead Ads automation is ON"
                      )
                    : t(
                        "onboarding.leadAds.toggleOff",
                        "Lead Ads automation is OFF"
                      )}
                </span>
              </label>
              {!isReadyForAutomation && (
                <p className="muted small">
                  {t(
                    "onboarding.leadAds.toggleDisabledHint",
                    "Connect both channels to turn this automation on."
                  )}
                </p>
              )}
            </div>
          </header>

          {templatesLoading ? (
            <p className="muted">
              {t("common.loading", "Loading")}
            </p>
          ) : (
            <>
              <div className="form-grid-2">
                <div className="form-field">
                  <label>
                    {t(
                      "whatsappTemplates.leadAutomation.phoneField",
                      "Phone field name"
                    )}
                  </label>
                  <input
                    type="text"
                    value={leadConfigDraft.phoneFieldName || ""}
                    onChange={(e) =>
                      setLeadConfigDraft((prev) => ({
                        ...prev,
                        phoneFieldName: e.target.value
                      }))
                    }
                    placeholder="phone_number"
                    required={enabled}
                    disabled={formDisabled}
                  />
                  <div className="muted small">
                    {t(
                      "whatsappTemplates.leadAutomation.phoneFieldHelp",
                      "Name of the form field that contains the phone number in the lead payload."
                    )}
                  </div>
                </div>

                <div className="form-field">
                  <label>
                    {t(
                      "whatsappTemplates.leadAutomation.consentField",
                      "Consent field name (optional)"
                    )}
                  </label>
                  <input
                    type="text"
                    value={leadConfigDraft.consentFieldName || ""}
                    onChange={(e) =>
                      setLeadConfigDraft((prev) => ({
                        ...prev,
                        consentFieldName: e.target.value
                      }))
                    }
                    placeholder="whatsapp_opt_in"
                    disabled={formDisabled}
                  />
                  <div className="muted small">
                    {t(
                      "whatsappTemplates.leadAutomation.consentFieldHelp",
                      "Name of the yes/no field used for WhatsApp consent."
                    )}
                  </div>
                </div>
              </div>

              <div className="form-field form-field-inline">
                <label className="booking-toggle">
                  <input
                    type="checkbox"
                    checked={leadConfigDraft.requiresWhatsappOptIn}
                    onChange={(e) =>
                      setLeadConfigDraft((prev) => ({
                        ...prev,
                        requiresWhatsappOptIn: e.target.checked
                      }))
                    }
                    disabled={formDisabled}
                  />
                  <span className="booking-toggle-slider" />
                  <span className="booking-toggle-label">
                    {t(
                      "whatsappTemplates.leadAutomation.requireOptIn",
                      "Require explicit WhatsApp opt-in"
                    )}
                  </span>
                </label>
              </div>

              <hr />

              <div className="form-field">
                <label>
                  {t(
                    "whatsappTemplates.leadAutomation.template",
                    "Template to send"
                  )}
                </label>
                <select
                  value={
                    leadConfigDraft.templateName &&
                    leadConfigDraft.templateLanguage
                      ? `${leadConfigDraft.templateName}:${leadConfigDraft.templateLanguage}`
                      : ""
                  }
                  onChange={(e) => {
                    const value = e.target.value;
                    setLeadConfigDraft((prev) => {
                      if (!value) {
                        return {
                          ...prev,
                          templateName: "",
                          templateLanguage: ""
                        };
                      }
                      const [name, lang] = value.split(":");
                      return {
                        ...prev,
                        templateName: name,
                        templateLanguage: lang
                      };
                    });
                  }}
                  disabled={formDisabled || approvedTemplates.length === 0}
                  required={enabled}
                >
                  <option value="">
                    {t(
                      "whatsappTemplates.leadAutomation.templatePlaceholder",
                      "Select an approved template"
                    )}
                  </option>
                  {approvedTemplates.map((tpl) => (
                    <option
                      key={tpl.id}
                      value={`${tpl.name}:${tpl.language}`}
                    >
                      {tpl.name} ({tpl.language})
                    </option>
                  ))}
                </select>
                <div className="muted small">
                  {t(
                    "whatsappTemplates.leadAutomation.templateHelp",
                    "Template used for new leads from Meta. Only approved templates are available."
                  )}
                </div>
                <div className="muted small" style={{ marginTop: "0.5rem" }}>
                  <Link
                    to={`/app/bots/${encodeURIComponent(
                      botId
                    )}/whatsapp-templates`}
                    target="_blank"
                    rel="noreferrer"
                  >
                    {t(
                      "onboarding.leadAds.manageTemplates",
                      "Manage WhatsApp templates in a separate page"
                    )}
                  </Link>
                </div>
              </div>

              {selectedTemplate && (
                <div className="leadads-template-preview">
                  <p className="muted small">
                    {t(
                      "onboarding.leadAds.previewLabel",
                      "Selected template:"
                    )}{" "}
                    <strong>
                      {selectedTemplate.name} ({selectedTemplate.language})
                    </strong>
                  </p>
                </div>
              )}

              <div className="onboarding-actions">
                <button
                  type="button"
                  className="btn-secondary"
                  onClick={handleBack}
                  disabled={saving}
                >
                  {t("onboarding.common.back", "Back")}
                </button>
                <button
                  type="button"
                  className="btn-primary"
                  onClick={handleNext}
                  disabled={saving}
                >
                  {saving
                    ? t("common.saving", "Saving")
                    : t("onboarding.leadAds.reviewAndPay", "Review plan & pay")}
                </button>
              </div>
            </>
          )}
        </section>
      </div>
    </OnboardingLayout>
  );
};

export default OnboardingLeadAdsPage;
